.RatingList .Cell .Cell__children {
  display: flex;
}

.RatingList .Cell .Div {
  flex: 1;
  padding: 6px;
}

/* Прячем иконки в шапке */
.RatingList .Cell:nth-child(1) .Avatar {
  opacity: 0;
}

/* Жирный заголовок столбцов */
.RatingList .Cell:nth-child(1) {
  font-weight: bold;
}

/* Первая колонка с аватарками и именами шире */
.RatingList .Cell .Div:nth-child(1) {
  flex: 3;

  /* Многоточие если текст не влазит */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/** Имя группы в ровень с именем и фамилией */
.RatingList .Cell__description {
  padding-left: 8px;
}

/** Ячейка с званием */
.RatingList .Rank {
  font-weight: bold;
  color: var(--text_secondary);
}

/** Ячейка с баллом */
.RatingList .Point {
  font-size: 18px;
}
