.Info .Cell .Cell__children {
  display: flex;
}

.Info .Cell .Div {
  flex: 1;
}

/* Прогрес званий */
.Rank {
  padding-top: 0;
  padding-bottom: 0;
}
.Rank .InfoRow__header {
  font-weight: bold;
  color: var(--header_text);
}
.Rank .InfoRow__footer {
  float: right;
  font-size: 14px;
  line-height: 16px;
  color: var(--text_secondary);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-top: 4px;
}

.ChecksList .Cell .Cell__children {
  display: flex;
}

.ChecksList .Cell .Div {
  flex: 1;
  padding: 6px;
}

/* Прячем иконки в шапке */
.ChecksList .Cell:nth-child(1) .Icon {
  opacity: 0;
}

/* Жирный заголовок столбцов */
.ChecksList .Cell:nth-child(1) {
  font-weight: bold;
}

/* Первая колонка с аватарками и именами шире */
.ChecksList .Cell .Div:nth-child(1) {
  flex: 2;

  /* Многоточие если текст не влазит */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Балы */
.ChecksList .Cell .Values {
  text-align: center;
}

